import React, { useEffect, useState } from 'react'
import styles from './FileDropzone.module.css';
import { useDropzone } from 'react-dropzone';
import upload from '../../Assets/upload.svg'

const FileDropzone = ({ setUploadedFiles, message }) => {

    const {getRootProps, getInputProps, open, acceptedFiles} = useDropzone({
		// Disable click and keydown behavior
		noClick: true,
		noKeyboard: true
    });

    useEffect(() => {
        setUploadedFiles(acceptedFiles)
    }, [acceptedFiles])
    
    const files = acceptedFiles.map((file, i) => (
		<p key={i}>
			{file.name}
			{/* {file.size} bytes */}
		</p>
	));

    return (
        <div className={styles.container}>
            <div {...getRootProps({className: 'dropzone'})} style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
                <input {...getInputProps()} />
                <img src={upload} alt='upload'/>
                <div className={styles.dragContainer}>
                    <p style={{paddingRight: 5}}>Drag and drop files here or</p>
                    <p style={{color: '#3B7DDD', textDecoration: 'underLine', cursor: 'pointer'}} onClick={open}>browse</p>
                </div>
                <p style={{color: '#6D6E6F', fontWeight: '400', fontSize: 13}}>Supported file format: XLSX</p>
            </div>
            <div style={{textAlign: 'center'}}>
                <p>{files}</p>
                <p>{message}</p>
            </div>
        </div>
    )
}

export default FileDropzone