import React, { useState } from 'react'
import styles from './ChangeEbitda.module.css'
import PopupHeader from '../PopupHeader/PopupHeader';
import BottomBtnsBar from '../BottomBtnsBar/BottomBtnsBar';
import { changeEbitdaValue } from '../../Services/APIs';
// import { useNavigate } from 'react-router-dom';

const ChangeEbitda = ({ setShowModal, setPopUpAppear, setTableHead, setNewData }) => {

	const [ebitdaVal, setEbitdaVal] = useState('');
	const [uploadingMessage, setUploadingMessage] = useState('')
    // const navigate = useNavigate();

	const handleSubmit = async(e) => {
		
		
		if (isNaN(parseInt(ebitdaVal)) || parseInt(ebitdaVal) < -99 || parseInt(ebitdaVal) > 100) {
			alert("EBITDA value should be an integer value between -99 and 100")
			return
		} else {
			setUploadingMessage('Calculations In Progress...')
			let errMsg = ''
			try{
				
            let formData = new FormData();
			formData.set("Adjusted_TTM_EBITDA_Current",ebitdaVal)
			console.log(ebitdaVal, "-----25")
			const res = await changeEbitdaValue(formData)
			console.log(res, "--------27")
			if (!res.error) {
				const response = res.response
				console.log(response, "-----31")
				if (!response.error) {
					const data = response.data
					console.log(data.generatedResults, "---------39")
					if(!data.error) {
						setTableHead(Object.keys(data.generatedResults[0]))
						setNewData(data.generatedResults)
						setShowModal(false)
						setPopUpAppear(false)
					} else {
						errMsg = data.errorMessage
					}
					setUploadingMessage('')
				} else {
					errMsg = response.errorMessage
				}
			} else {
				errMsg = res.errorMessage
			}
			}
			catch(error){
				errMsg = error.message
			}
			if (errMsg !== '') {
				alert(errMsg)
				console.log(errMsg)
				setUploadingMessage('')
			}
			
		}
		
		// navigate('/generatedData')
	}

	const closeModal = () => {
		setShowModal(false)
		setPopUpAppear(false)
	}
    
	return (
		<div className={styles.rootContainer}>
			<div className={styles.subRootContainer}>
				<PopupHeader headText={'Change EBITDA'} closeModal={closeModal} />
        		<div className={styles.inputContainer}>
					<input placeholder='EBITDA value supported value -99 to 100' type='text' value={ebitdaVal} onChange={(e) => setEbitdaVal(e.target.value)} />
					<p>{uploadingMessage}</p>
				</div>
				<br/>
				<BottomBtnsBar continueAction={handleSubmit} cancelAction={closeModal} />
				<br/>
			</div>
		</div>
	)
}

export default ChangeEbitda