import React, { useEffect, useState, useRef } from 'react'
import styles from '../ImportedData/ImportedData.module.css'
import NavBar from '../../Component/NavBar/NavBar'
import arrow from "../../Assets/arrow.svg";
import excelIcon from '../../Assets/excel.svg'
import { DataTable } from "../../Component/DataTable/DataTable";
import AddNewBorrower from '../../Component/AddNewBorroweCard/AddNewBorrower';
import ChangeEbitda from '../../Component/ChangeEBITDACard/ChangeEbitda';
import { getAdjustedBorrowingValue, getExcessConcentration, getGeneratedResult } from '../../Services/APIs';
import { useNavigate } from 'react-router-dom';
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';


export const GeneratedResult = () => {
	
  	const navigate = useNavigate();
    const [showAddNewBorrowerModal, setShowAddNewBorrowerModal] = useState(false)
    const [showChangeEbitdaModal, setShowChangeEbitdaModal] = useState(false)
    const [newData, setNewData] = useState([])
    const [tableHead, setTableHead] = useState([])
    const [popUpAppear, setPopUpAppear] = useState(false)
    const [header, setHeader] = useState("Generated result")
    const [analysisSelectValue,setAnalysisSelectValue]=useState("")
    const [resultValue, setResultValue] = useState("Intermediate results")


    const popUpRef = useRef(null);
   

    useEffect(() => {
       function allResults(){
        if(resultValue === "Intermediate results"){
          setHeader("Generated result")
          generatedResults();
         }
         else if (resultValue === "Adjusted borrowing value"){
          setHeader("Adjusted borrowing value")
          adjustedBorrowingResults();
         }
         else if (resultValue === "Excess concentration"){
          setHeader("Excess concentration results")
          excessConcentrationResults();
         }
       }
       allResults();
    }, [resultValue]);


    useEffect(()=>{
      if (analysisSelectValue === "addNewBorrower") {
        setShowChangeEbitdaModal(false)
        setShowAddNewBorrowerModal(true)
        setPopUpAppear(true)
      } else if (analysisSelectValue === "changeEbitda") {
        setShowAddNewBorrowerModal(false)
        setShowChangeEbitdaModal(true)
        setPopUpAppear(true)
      }  
    }, [analysisSelectValue])

   

    function downloadXlsx() {
      const data = newData
        
    
    
      const sheet = XLSX.utils.json_to_sheet(data);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, sheet, 'Sheet1');
      const xlsxFile = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
    
      const blob = new Blob([xlsxFile], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
      FileSaver.saveAs(blob, 'data.xlsx');
    }

    async function generatedResults() {
      try {
        let res = await getGeneratedResult()
        console.log(res, "------32")
        if (res && !res.error) {
          console.log(res.response.data.generatedResults, "-----------34")
          setNewData(res.response.data.generatedResults)
          setTableHead(Object.keys(res.response.data.generatedResults[0]))
        } else {
          console.log(res.errorMessage)
        }
      } catch(error){
        console.log(error.message, "-----------22")
      }
    }

    async function adjustedBorrowingResults() {
      try {
        let res = await getAdjustedBorrowingValue()
        // let response = res.JSON.parse()
        // // console.log(response, "------83")
        console.log()
        console.log(res, "------82")
        if (res && !res.error) {
        
          setNewData(res.response.data.results)
          setTableHead(Object.keys(res.response.data.results[0]))
        } else {
          console.log(res.errorMessage)
        }
      } catch(error){
        console.log(error.message, "-----------91")
      }
    }

    async function excessConcentrationResults() {
      try {
        let res = await getExcessConcentration()
        console.log(res, "------98")
        if (res && !res.error) {
          console.log(res.response.data.results, "-----------100")
          setNewData(res.response.data.results)
          setTableHead(Object.keys(res.response.data.results[0]))
        } else {
          console.log(res.errorMessage)
        }
      } catch(error){
        console.log(error.message, "-----------107")
      }
    }
    

    const resultOptionHandler = (e) => {
      setResultValue(e.target.value)
    }
    
    console.log(resultValue, "-------51")

    const selectOptionHandler = (e) => {
      setAnalysisSelectValue(e.target.value)
    }
  
    
	// useEffect(() => {
	// 	// only add the event listener when the dropdown is opened
	// 	if (!popUpAppear) return;
	// 	function handleClick(event) {
		
	// 		if (popUpRef.current && !popUpRef.current.contains(event.target)) {
	// 			setPopUpAppear(false);
	// 			setShowAddNewBorrowerModal(false);
	// 			setShowChangeEbitdaModal(false);
	// 		}
	// 	}
	// 	window.addEventListener("click", handleClick);
	// 	// clean up
	// 	return () => window.removeEventListener("click", handleClick);
	// }, [popUpAppear]);

  return (
	<>
  
	<div className={styles.importedData}>
      <NavBar />
      <div className={styles.container} style={{pointerEvents: popUpAppear ? 'none' : ''}}>
        <div className={styles.mainContainer}>
          <div className={styles.head}>
            <div className={styles.mainHead}>
              <img src={arrow} alt="arrowIcon" onClick={() => navigate(-1)} />
              <h2>{header}</h2>
            </div>
            <div className={styles.generatItems}>
              <div className={styles.downloadBtn} onClick={downloadXlsx}><img src={excelIcon} className={styles.excelIcon} alt="excel"/>Download result</div>
              <select name="results" id="results" value={resultValue} className={styles.resultsDropdown } onChange={(e) => resultOptionHandler(e)} >
                <option className={styles.dropdownItem} value="Intermediate results">Intermediate results</option>
                <option  className={styles.dropdownItem} value="Adjusted borrowing value">Adjusted borrowing value</option>
                <option className={styles.dropdownItem} value="Excess concentration">Excess concentration</option>
              </select>
              <select name="analysis" id="analysis" value={analysisSelectValue} className={styles.resultsDropdown} onChange={(e) => selectOptionHandler(e)}>
                <option className={styles.dropdownItem} value="whatIfAnalysis">What if analysis</option>

                <option className={styles.dropdownItem} value="addNewBorrower" >Add new borrower</option>
                <option className={styles.dropdownItem} value="changeEbitda">Change EBITDA</option>
              </select>
             </div>
          </div>
          <div style={{overflowX: 'scroll'}}>
          <DataTable tableHeadData={tableHead} tableData={newData}/>
          </div>
        </div>

      </div>
		<div className={styles.modalContainer} ref={popUpRef}>
			{showAddNewBorrowerModal &&
				<AddNewBorrower setShowModal={setShowAddNewBorrowerModal} setPopUpAppear={setPopUpAppear} setTableHead={setTableHead} 
          setNewData={setNewData}
        />}
			{showChangeEbitdaModal &&
				<ChangeEbitda setShowModal={setShowChangeEbitdaModal} setPopUpAppear={setPopUpAppear} setTableHead={setTableHead} setNewData={setNewData}/>
			}
		</div>
    </div>
  
	<div className={styles.greyoutBg} style={{width: popUpAppear ? '100%' : '', height: popUpAppear ? '100%' : ''}}></div>
  
	</>

  )
}