import React from 'react'
import styles from './BottomBtnsBar.module.css';

const BottomBtnsBar = ({ continueAction = () => {}, cancelAction = () => {}, uploading = false }) => {

    return (
        <div className={styles.btnContainer}>
            <button className={!uploading ? styles.continueBtn : styles.continueBtnDisabled}
                onClick={() => continueAction()} disabled={uploading}>
                Continue
            </button>
            <button className={styles.cancelBtn} onClick={() => cancelAction()} >Cancel</button>
        </div>
    )
}

export default BottomBtnsBar