import React, { useState } from 'react'
import styles from './DataSourceCard.module.css';
import { useNavigate } from 'react-router-dom';
import { fileUpload } from '../../Services/APIs';
import PopupHeader from '../PopupHeader/PopupHeader';
import FileDropzone from '../FileDropzone/FileDropzone';
import BottomBtnsBar from '../BottomBtnsBar/BottomBtnsBar';
import sampleFile from '../../Assets/sampleFile/BaseData.xlsx'

const DataSourceCard = () => {

    const [selectedValue, setSelectedValue] = useState("option1");
    const navigate = useNavigate();
	const [uploadedFiles, setUploadedFiles] = useState([])
    const [uploadingMessage, setUploadingMessage] = useState('')



	function handleDownload() {
		fetch(sampleFile)
		  .then(response => response.blob())
		  .then(blob => {
			const url = URL.createObjectURL(blob);
			const link = document.createElement('a');
			link.href = url;
			link.setAttribute('download', 'sampleFile.xlsx');
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);
		  });
	  }
	  

    const handleOptionChange = (event) => {
		setSelectedValue(event.target.value);
    };

	const uploadFile = async() => {
		if (uploadedFiles.length <= 0) {
			alert('Upload xlsx file or cancel dialog box')
			return
		}
		// if (uploadedFiles[0].type !== 'text/xlsx') {
		// 	alert('Only xlsx files allowed')
		// 	return
		// }
        setUploadingMessage('File uploading...')
		let errMsg = ''
        try {
			let formData = new FormData();
			formData.append("file", uploadedFiles[0])
			const res = await fileUpload(formData)
			if (!res.error) {
				const response = res.response
				if (!response.error) {
					const data = response.data
					console.log(data, "---------39")
					if(data.conditionSatisfied) {
						// setImportedData()
						navigate('/importedData', {state: data.importedData})
						
					} else {
						navigate("/generatedData")
					}
				} else {
					errMsg = response.errorMessage
				}
			} else {
				errMsg = res.errorMessage
			}
		} catch (error) {
			errMsg = error.message
		}
		if (errMsg !== '') {
			alert(errMsg)
			console.log(errMsg)
			setUploadingMessage('')
		}
    }

  	return (
		<div className={styles.rootContainer}>
			<div style={{width: 606}}>
				<PopupHeader headText={'Select data Source'} />
        		<div className={styles.options}>
        			<p>Available Options</p>
        			<div className={styles.radio}>
        				<div>
        					<input type="radio" value="option1" checked={selectedValue === "option1"}
          						onChange={handleOptionChange}  />
      						Use existing data from Pepper
      					</div>
      					<div>
							<input type="radio" value="option2" checked={selectedValue === "option2"}
								onChange={handleOptionChange} />
        					Upload from excel file
      					</div>
        			</div>
        			<br/>
					{selectedValue === 'option2' &&
						<>
							<FileDropzone setUploadedFiles={setUploadedFiles} message={uploadingMessage} />
							<p onClick={handleDownload} className={styles.sampleDownload}>Download sample file template</p>
						</>
					}
					<BottomBtnsBar continueAction={uploadFile} uploading={uploadingMessage !== ''} />
				</div>
				<br/>
				<br/>
			</div>
    	</div>
  	)
}

export default DataSourceCard