import axios from "axios";

const BASE_URL = "https://onkarbhondwe.pythonanywhere.com/api/leveragemodel"
// const BASE_URL = "http://192.168.58.140:5000/api/leveragemodel"

const fileUpload = async(formData) => {
    try {
        //call api
        const res = await axios.post(`${BASE_URL}/uploadDataSource`, formData)
        return {error: false, response: res}
    } catch (error) {
        return {error: true, errorMessage: error.message}
    }
}

const getGeneratedResult = async() => {
    try {
        //call api
        const res = await axios.get(`${BASE_URL}/generatedResult`, {
            headers: {
                "Content-Type": "application/json"
            }
        })
        return {error: false, response: res}
    } catch (error) {
        return {error: true, errorMessage: error.message}
    }
}


