import React, { useState } from 'react'
import styles from './AddNewBorrower.module.css'
import PopupHeader from '../PopupHeader/PopupHeader';
import FileDropzone from '../FileDropzone/FileDropzone';
import BottomBtnsBar from '../BottomBtnsBar/BottomBtnsBar';
import portfolioExample from '../../Assets/sampleFile/PortfolioExample.xlsx'
import { addNewBorrower } from '../../Services/APIs';
import { useNavigate } from 'react-router-dom';

const AddNewBorrower = ({ setShowModal, setPopUpAppear, setTableHead, setNewData }) => {

    // const navigate = useNavigate();
    const [selectedValue, setSelectedValue] = useState("option2");
	const [uploadingMessage, setUploadingMessage] = useState('')
	const [uploadedFiles, setUploadedFiles] = useState([])
	const navigate = useNavigate();
    
    const handleOptionChange = (event) => {
    	setSelectedValue(event.target.value);
    };

	function handleDownload() {
		fetch(portfolioExample)
		  .then(response => response.blob())
		  .then(blob => {
			const url = URL.createObjectURL(blob);
			const link = document.createElement('a');
			link.href = url;
			link.setAttribute('download', 'portfolioExample.xlsx');
			document.body.appendChild(link);
			link.click();
			document.body.removeChild(link);
		  });
	  }

	  const uploadFile = async() => {
		if (uploadedFiles.length <= 0) {
			alert('Upload xlsx file or cancel dialog box')
			return
		}
		// if (uploadedFiles[0].type !== 'text/xlsx') {
		// 	alert('Only xlsx files allowed')
		// 	return
		// }
        setUploadingMessage('File uploading...')
		let errMsg = ''
        try {
			let formData = new FormData();
			formData.append("file", uploadedFiles[0])
			const res = await addNewBorrower(formData)
			if (!res.error) {
				const response = res.response
				if (!response.error) {
					const data = response.data
					console.log(data, "---------39")
					if(!data.error) {
						// setImportedData()
						setTableHead(Object.keys(data.generatedResults[0]))
						setNewData(data.generatedResults)
						setShowModal(false)
						setPopUpAppear(false)
					} else {
						errMsg = data.errorMessage
					}
					setUploadingMessage('')
				} else {
					errMsg = response.errorMessage
				}
			} else {
				errMsg = res.errorMessage
			}
		} catch (error) {
			errMsg = error.message
		}
		if (errMsg !== '') {
			alert(errMsg)
			console.log(errMsg)
			setUploadingMessage('')
		}
    }

	// const handleSubmit = (e) => {
	// 	e.preventDefault()
	// 	// call api
	// 	// if (uploadedFiles.length <= 0 || uploadedFiles[0].type !== 'text/csv') {
	// 	// 	alert('Only csv files allowed')
	// 	// 	return
	// 	// }
	// 	alert('Call API Endpoint')
	// 	// navigate('/generatedData')
	// }

	const closeModal = () => {
		setShowModal(false)
		setPopUpAppear(false)
	}
    
	return (
		<div className={styles.rootContainer}>
			<div className={styles.subRootContainer}>
			<PopupHeader headText={'Add New Borrower'} closeModal={closeModal} />
        		<div className={styles.options}>
         			<p>Available Options</p>
         			<div className={styles.newRadio}>
						<div>
							<input type="radio" value="option1" checked={selectedValue === "option1"}
								onChange={handleOptionChange} disabled={true} />
							Use existing data from Pepper
      					</div>
      					<div>
        					<input type="radio" value="option2" checked = {selectedValue === "option2"}
          						onChange={handleOptionChange} />
        					Upload from excel file
      					</div>
						<div>
							<input type="radio" value="option3" checked={selectedValue === "option3"}
								onChange={handleOptionChange} disabled={true} />
							Add data manually
						</div>
        			</div>
        			<br/>
      				{selectedValue === 'option2' &&
      					<>
         					<FileDropzone setUploadedFiles={setUploadedFiles} message={uploadingMessage} />
    						<p className={styles.sampleDownload} onClick={handleDownload}>Download sample file template</p>
    					</>
					}
					<br/>
					<BottomBtnsBar continueAction={uploadFile} cancelAction={closeModal} uploading={uploadingMessage !== ''} />
				</div>
        		<br/>
        		<br/>
			</div>
		</div>
	)
}

export default AddNewBorrower