import React, { useState, useEffect, useRef } from "react";
import NavBar from "../../Component/NavBar/NavBar";
import styles from "./ImportedData.module.css";
import arrow from "../../Assets/arrow.svg";
import infoIcon from "../../Assets/info.svg";
import { DataTable } from "../../Component/DataTable/DataTable";
import { useNavigate } from 'react-router-dom';
import { Link, useLocation } from "react-router-dom";
import ReUploadFile from "../../Component/ReUploadFileCard/ReUploadFile";
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';

const ImportedData = () => {

 

  // useEffect(() =>{
  //   fetchData();
  // },[])
  
  
  
  
 
  const navigate = useNavigate();
  const {state} = useLocation();
  console.log(state, "----------28")
  const [tableData, setTableData] = useState([])
  const [tableHead, setTableHead] = useState([])
  const [showPopUp, setShowPopUp] = useState(false)
  const [showModal, setShowModal] = useState(false)
  console.log("Table Data:",tableData)
  
  // const tableHead = Object.keys(tableData[0])
  useEffect(() => {
    setTableData(state)
    setTableHead(Object.keys(state[0]))
  }, [])

const popUpRef = useRef(null);

  
  console.log(tableData)
  function handleDownload() {
		const data = tableData
		const sheet = XLSX.utils.json_to_sheet(data);
		const workbook = XLSX.utils.book_new();
		XLSX.utils.book_append_sheet(workbook, sheet, 'Sheet1');
		const xlsxFile = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
		const blob = new Blob([xlsxFile], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
		FileSaver.saveAs(blob, 'data.xlsx');
	  }


  const handleUploadClick = () => {
	setShowModal(true)
	setShowPopUp(true)
  }

  return (
	<>
    <div className={styles.importedData}>
      <NavBar />
      <div className={styles.container}>
        <div className={styles.mainContainer}>
          <div className={styles.head}>
            <div className={styles.mainHead}>
            
              <img src={arrow} onClick={() => navigate(-1)} alt="arrowIcon" />
             
              <h2>Imported data</h2>
            </div>
            <div>
              <Link to="/generatedData"><button className={styles.submitButton}>Proceed</button></Link> 
            </div>
          </div>
          <div className={styles.alertBox}>
            <img src={infoIcon} alt="infoIcon" className={styles.infoIcon} />
            <span className={styles.textContainer}>
              To update VAE <p className={styles.links} onClick={handleDownload}>Download</p> existing
              file and <p className={styles.links} onClick={handleUploadClick}>re-upload</p> with updated
              data
            </span>
          </div>
          <DataTable tableHeadData={tableHead} tableData={tableData}/>
        </div>
      </div>
	  <div className={styles.modalContainer} ref={popUpRef}>
	  {showModal &&
       <ReUploadFile setShowModal={setShowModal} setPopUpAppear={setShowPopUp} setTableHead={setTableHead} setTableData={setTableData}/>
	  }
	  </div>
    </div>
	<div className={styles.greyoutBg} style={{width: showPopUp ? '100%' : '', height: showPopUp ? '100%' : ''}}></div>
	</>
  );
};

export default ImportedData;
