
import './App.css';
import { Routes, Route } from "react-router-dom";

import ImportedData from './Screens/ImportedData/ImportedData';
import Welcome from './Screens/WelcomeScreen/Welcome';
import { GeneratedResult } from './Screens/GeneratedResult/GeneratedResult';

function App() {
  return (
    
    <div className="App">
   <Routes>
   <Route exact path="/" element={<Welcome />}></Route>
    <Route exact path="/importedData" element={<ImportedData />}></Route>
    <Route exact path="/generatedData" element={<GeneratedResult />}></Route>
   </Routes>
    </div>
  );
}

export default App;
