import React from 'react'
import styles from '../DataTable/DataTable.module.css'

export const DataTable = ({tableHeadData,tableData}) => {
    
  return (
    <table >
    <thead>
            <tr>
              {tableHeadData.map((el) => (
                <th>{el}</th>
              ))}
            </tr>
            </thead>
            {tableData.map(el=>
              <tr>
              {tableHeadData.map(t => {
                  return <td>{(el[t]).toString()}</td>
              })}
              </tr>
            )}
           
          </table>
  )
}
